/**
 * cookie
 * time:2021/2/19
 */
export default {
  /*
     *cookie
     *key  val   time
     * */
  set (key, val, time) {
    if (time) {
      const date = new Date() // 
      const hours = time // daten
      date.setTime(date.getTime() + hours * 3600 * 1000) // cookie
      document.cookie = key + '=' + val + ';expires=' + date.toGMTString() // cookie
    } else {
      document.cookie = key + '=' + val
    }
  },
  /*
  *cookie
   *key
   * */
  get (key) {
    const getCookie = document.cookie.replace(/[ ]/g, '') // cookiecookie
    const arrCookie = getCookie.split(';') // cookie"" cookiearrCookie
    let tips // tips
    for (let i = 0; i < arrCookie.length; i++) { // forcookietips
      const arr = arrCookie[i].split('=') // cookie""cookiearr
      if (key === arr[0]) { // arr[0]cookietips
        tips = arr[1] // cookietips
        break // for
      }
    }
    return tips
  },
  /*
   *cookie
   *key
   * */
  del (key) { // cookie
    const date = new Date() // 
    date.setTime(date.getTime() - 10000) // date
    document.cookie = key + '=v; expires =' + date.toGMTString()// cookie
  }
}
